.header-top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.header-top__menu {
  font-size: 16px;
  position: relative;
  padding-left: 36px;
  margin: 0;

  &::before {
    content: "";
    position: absolute;
    width: 24px;
    height: 1px;
    background-color: $purple-header;
    left: 0;
    top: 2px;
    box-shadow: 0 6px 0 $purple-header;
  }

  &::after {
    content: "";
    position: absolute;
    width: 14px;
    height: 1px;
    background-color: $purple-header;
    left: 0;
    top: 14px;
  }
}

.header-top__phone {
  font-size: 16px;
  padding: 0;
  padding-top: 5px;
  margin: 0;
}
